body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #1d1d1f;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.appleicon {
  padding: 0 0.5rem;
}

.navbar {
  background-color: rgba(0, 0, 0, 0.8);
}
.navbar-padding {
  margin: 0 auto;
  max-width: 980px;
  padding: 0 22px;
  background-color: transparent;
  box-shadow: none;
}

.navlinks,
.navlinks-right {
  font-size: 0.9rem;
  padding: 0 1rem;
  color: #f5f5f7;
}

.toolbar {
  min-height: 44px;
  padding: 0;
}

.divider {
  border-bottom: 1px solid #c1c1c1;
  padding-top: 1.5rem;
}

.location-search {
  padding: 2rem;
}

.location-searchbar {
  padding: 1rem 0;
}

.location-searchbar-label {
  font-size: 1.5rem;
  font-weight: 700;
}

.product-name {
  font-size: 1.2rem;
  font-weight: 600;
  padding: 1rem 0;
}

.selected-row {
  background-color: #daecff !important;
}

.action-button {
  text-transform: capitalize;
  height: 2rem;
  padding: 0.5rem 1.5rem;
  border-radius: 0.5rem;
  margin-left: 0.5rem;
}

.appt-button {
  background-color: #0066cc;
  color: #f5f5f7;
}

.available {
  color: #008905 !important;
}

.background-grey {
  background: "#fafafa";
}

.close-button {
  background-color: #c2c2c2;
  color: #fff;
  padding: 0.25rem;
}

.dialog-content {
  padding: 1rem;
}

.reserve-button {
  background-color: #1d1d1f;
  color: #f5f5f7;
}

.product-title {
  padding: 1rem;
}

.selected-row {
  background-color: #daecff !important;
}

.table-header {
  font-size: 0.8rem;
  font-weight: 500;
  border-bottom: 1px solid #1d1d1f;
}

.unavailable {
  color: #890000 !important;
}

.view {
  color: #0066cc !important;
}

.view:hover,
.view:focus {
  text-decoration: underline !important;
  cursor: pointer;
}

.active {
  border-bottom: 4px solid#1D1D1F;
}

.filter-icon-label {
  display: flex;
}

.filter-icon-text {
  padding: 0 0.5rem;
}

.filter-icon {
  align-self: center;
}

.filterheader {
  padding: 0 2rem;
  min-height: 50px;
  display: flex;
  align-items: center;
}

.filterheader,
.header {
  border-bottom: 1px solid #c1c1c1;
}

.header {
  background-color: #f2f2f2;
}

.header-padding {
  margin: 0 auto;
  max-width: 980px;
  /* padding: 0 22px; */
  background-color: transparent;
  min-height: 230px;
}

.heading {
  padding-top: 2rem;
  float: left;
}

.reset-button,
.reset-button:hover {
  text-transform: capitalize;
  background-color: #1d1d1f;
  color: #f5f5f7;
}

.searchbar-group {
  display: flex;
  justify-content: flex-start;
  padding-top: 2rem;
  align-items: center;
}

.searchbar {
  padding-right: 1rem;
  font-weight: 800;
}

.search-type-button {
  float: right;
  text-transform: capitalize;
  margin-top: 3rem;
}

.MuiInputBase-input {
  height: 1rem;
}

.MuiOutlinedInput-root.Mui-focused .MuiOutlinedInput-notchedOutline {
  border-color: #c1c1c1 !important;
}

.MuiFormLabel-root.Mui-focused {
  color: #1d1d1f !important;
}

#product-searchbar {
  height: 1rem;
}

